let simpleIndex = 0;

const step = 3;



refs.clearBtn.addEventListener('click', clearText);
refs.defaultBtn.addEventListener('click', setDefault);
refs.decipherBtn.addEventListener('click', getDecipher);
refs.encryptBtn.addEventListener('click', getEncrypt);

function getDecipher() {
  const data = refs.text.value
    .toLowerCase()
    .split('')
    .map(item => {

      const currentIndex = alphabet.findIndex(letter => letter === item);

      console.log('currentIndex', currentIndex);

      if (currentIndex !== -1) {
        const newPos = currentIndex - step;

        if (newPos >= 0) {
          return alphabet[newPos]
        } else {
          // console.log(newPos)

          console.log('newPos', newPos);

          console.log('alphabet.length', alphabet.length)

          const newStep = Math.abs(Math.abs(newPos) - (alphabet.length));
          console.log('newStep',newStep)
          return alphabet[newStep]
        }

      } else return item;
    }).join('')


  renderResult(data);
}

function getEncrypt() {
  const data = refs.text.value
    .toLowerCase()
    .split('')
    .map(item => {

      const currentIndex = alphabet.findIndex(letter => letter === item);

      if (currentIndex !== -1) {
        const newPos = currentIndex + step;

        if (newPos <= alphabet.length - 1) {
          return alphabet[newPos]
        } else {
          const newStep = newPos - alphabet.length;
          return alphabet[newStep]
        }

      } else return item;
    }).join('')


  renderResult(data);
}

function clearText() {
  refs.text.value = '';
  refs.result.innerText = 'Ð ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚ Ð±ÑƒÐ´Ðµ Ñ‚ÑƒÑ‚';
}

function setDefault() {

  simpleIndex += 1;
  let text = '';

  if (simpleIndex < simpleData.length) {
    text = simpleData[simpleIndex]
  } else {
    simpleIndex = 0;
    text = simpleData[simpleIndex]
  }

  refs.text.value = text;
}

function renderResult(data) {
  console.log(data);
  refs.result.innerText = data[0]?.toUpperCase() + data?.slice(1);
}

